@media screen and (max-width: 1400px), screen and (max-device-width: 1400px){
  body{
    .header__fixedNav{
      ul{
        li{
          &:nth-child(2){
            input{
              height: 30px;
              line-height: 30px;
            }
            i{
              font-size: 1em;
            }
          }
          &:nth-child(3){
            width: 16%;
          }
          &:nth-child(4){
            width: 6%;
          }
          &:nth-child(5){
            width: 13%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1020px), screen and (max-device-width: 1020px){
  body{
    .header__fixedNav{
      ul{
        li{
          &:nth-child(1){
            a{
              i{
                padding: 0 5px 0 10px;
              }
            }
          }
          &:nth-child(2) {
            input {
              width: 80%;
            }
          }
          &:nth-child(4){

          }
        }
      }
    }
  }
}

@media screen and (max-width: 1400px), screen and (max-device-width: 1400px){
  body{
    .header__fixedNav{
      ul{
        li{
          &:nth-child(2){
            width: 45%;
          }
          &:nth-child(3){
            width: 22%;
          }
          &:nth-child(4){
            width: 10%;
          }
          &:nth-child(5){
            width: 13%;
          }
        }
      }
    }
  }
}